.themeSwitcher {
  display: flex;
}
.button {
  width: 50px;
  height: 25px;
  border-radius: 200px;
  cursor: pointer;
  position: relative;
  &::before {
    position: absolute;
    content: "";
    background-color: #fff;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin: 2.5px 3px;
    transition: all 0.2s;
  }
}
input:checked + .button::before {
  transform: translateX(24px);
}
input {
  display: none;
}
