@import url("./Nullify.scss");

@import url("https://fonts.googleapis.com/css2?family=Playfair:wght@400;500;700&family=Roboto:wght@400;500;700&family=Rubik:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;0,500;0,700;1,500&display=swap");
#root {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.App {
  max-width: 1250px;
  padding: 0px 15px;
  margin: 0 auto;
  width: 100%;
}
body[data-theme="dark"] {
  background-color: hsl(213, 27%, 8%);
  color: #fff;
}
body[data-theme="light"] {
  background-color: hsl(0, 0%, 98%);
  color: black;
}
body[data-font="Rubik"] {
  font-family: Rubik;
}
body[data-font="Mono"] {
  font-family: Roboto Mono;
}
body[data-font="Playfair"] {
  font-family: Playfair;
}
ul.list {
  li {
    margin-left: 25px;
    position: relative;
    &:before {
      content: "";
      width: 5px;
      height: 5px;
      position: absolute;
      border-radius: 50%;

      top: 30%;
      left: -20px;
      background-color: #a751bd;
    }
  }
}
